/* Terminal.css */
.terminal-container {
  padding: 20px;
  display: flex;
  flex-direction: column;
  position: relative;
  align-items: flex-start;
  width: 100%;
  height: 100%;
  overflow: auto;
  color:#ebdbb2;
}

.terminal-window {
  position: relative;
}


input[type="text"] {
  background-color: transparent;
  border: none;
  outline: none;
  color:#ebdbb2;
  font-family: monospace;
  font-size: inherit;
  padding: 0;
}


.input-prefix {
  color:#ebdbb2;
  margin-right: 0px;
}

.output {
  color:#ebdbb2;
  font-family: monospace;
  white-space: pre-wrap;
  margin: 10px 0; /* Agrega margen superior e inferior para separar del prompt */
}



.user-prompt {
  display: flex;
  align-items: center;
}


.input-container {
  display: flex;
  align-items: center;
  background-color: transparent;
  border-top: 1px solid #00ff00;
  padding-top: 5px;
  padding-left: 5px;
  width: calc(100% - 20px);
}

.prompt {
  display: flex;
  align-items: center;
  font-family: monospace;
  color: #d65d0e;
  color: #b8bb26;
  color: #689d6a;
  color: #b16286;
  color:#8ec07c;
  margin: 0 0 5px 0; /* Ajusta los márgenes superior e inferior */
  max-width: 641px;
}

.input-prompt {
  display: flex;
  align-items: flex-start;
  margin-left: auto; /* Empuja el input-prompt a la derecha */
  margin-right: 5px; /* Agrega un margen derecho para separar del input */
  flex-grow: 1; /* Permite que el input-prompt ocupe el espacio restante */
}

.history-entry {
  display: flex;
  flex-direction: column; /* Cambio a flex-direction: column */
  font-family: monospace;
  color: #00ff00;
  margin-bottom: 5px;
}

.history-prompt-command {
  display: flex; /* Cambio a flex para alinear prompt y command */
  align-items: center; /* Alinea verticalmente */
  color: #00ff00; /* Color del prompt y command */
}

.history-prompt {
  margin-right: 5px; /* Margen entre prompt y command */
}

.history-command {
  margin-left: 5px; /* Margen entre prompt y command */
  color:#ebdbb2;
}

.history-output {
  color:#ebdbb2;
  white-space: pre-wrap;
  margin: 0;
  margin-left: 10px; /* Alinea history-output al margen izquierdo */
  text-align: left; /* Agrega alineación izquierda para el texto */
}
